import * as React from 'react';
import { Component, sharedComponentData } from 'react-simplified';
import { HashRouter, BrowserRouter, Route,  NavLink } from 'react-router-dom';
import ReactDOM from 'react-dom';
import { createHashHistory, createBrowserHistory } from 'history';
//import 'bootstrap/dist/css/bootstrap.min.css';
import "@fortawesome/fontawesome-free"


//const history = createHashHistory();
const history = createBrowserHistory();
const url = "http://localhost:5001/harmoni-6/us-central1/webApi";

class Test extends Component{
    render(){
        return(
            <div>
                <button onClick={this.test()}>Test webapp</button>
                <p>Go to git: <a className={"git"} href={"https://gitlab.stud.idi.ntnu.no/jakoblm/harmoni"}>
                    <i style={{fontSize: "40px"}} className="fab fa-github"/>
                </a> </p>
            </div>
        )
    }

    test(){
        fetch(url, {
            method: "POST",
            body: JSON.stringify({ }),
            headers: {
                'Content-Type': 'application/json; charset=utf-8',
                'x-access-token': 'undefined',
                "Access-Control-Allow-Origin": '*'
            }
        })
            .then(res => alert(res.body))
            .catch(err => alert("an error occured"));
    }
}

const root = document.getElementById('root');
if (root)
    ReactDOM.render(
    <BrowserRouter>
    <div>
    <Route exact path="/" component={Test} />
    </div>
    </BrowserRouter>,
root
);